import React from 'react';
import { User } from "../datatypes"
import { Link, Stack } from "@fluentui/react"

export interface ILoginStatus {
  user: User|null;
  login_fn: () => void;
  logout_fn: () => void;
}

export function LoginStatus(props: ILoginStatus) {
  let linkStyle = {root: {color: "white", fontSize: "small", textDecoration: "underline"}}
  if (props.user) {
    return (
      <Stack horizontal>
        <div>{props.user.name}</div>
        <div style={{paddingLeft: "10pt"}}>
          <Link styles={linkStyle} onClick={props.logout_fn}>Logout</Link>
        </div>
      </Stack>
    )
  } else {
    return (
      <Stack horizontal>
        <div>Not logged in</div>
        <div>
          <Link styles={linkStyle} onClick={props.login_fn}>Login</Link>
        </div>
      </Stack>
    )
  }
}
