import { SenseTreeNode, SenseTreePath, CwnSense, SenseTree } from "../datatypes";
import { IGroup, getParent } from "@fluentui/react";
import { chown } from "fs";


export interface GroupedSenses {
  senses: CwnSense[];
  groups: IGroup[];
}

export function countLeaves(node: SenseTreeNode) {
  if (node.children.length > 0) {
    let count: number = node.children
      .map((x) => x.node_type === "group" ? countLeaves(x) : 1)
      .reduce((sum, x) => sum + x, 0);
    return count;
  } else {
    return 0;
  }
}

export function toGroup(node: SenseTreeNode, depth = 0) {

  let group_members = node.children
    .filter((x) => x.node_type === "group")
    .map((x) => toGroup(x, depth + 1))


  let group = {
    key: node.node_id,
    name: node.title,
    data: {
      tag: node.tag,
      description: node.description,
      allCount: countLeaves(node)
    },
    level: depth,
    children: group_members
  } as IGroup;

  return group;
}

export function findNode(rootNode: SenseTreeNode,
  predicate: (node: SenseTreeNode) => boolean): SenseTreeNode | null {

  const nodeList = findNodes(rootNode, predicate);
  const node = nodeList.length > 0 ? nodeList[0] : null;

  return node;

}

export function findNodes(rootNode: SenseTreeNode,
  predicate: (node: SenseTreeNode) => boolean): SenseTreeNode[] {

  let nodeTraces = findNodeTrace(rootNode, predicate);
  let nodes = nodeTraces.map((path) => {
    return path[path.length - 1]
  });
  return nodes;
}

function findNodeTrace(
  rootNode: SenseTreeNode,
  predicate: (node: SenseTreeNode) => boolean): SenseTreePath[] {

  let buf: [SenseTreeNode, SenseTreePath][] = [[rootNode, []]];
  let results: SenseTreePath[] = [];
  while (buf.length > 0) {
    const [cur_node, path] = buf.pop() as [SenseTreeNode, SenseTreePath];
    const cur_path = [...path, cur_node];
    if (predicate(cur_node)) {
      results.push(cur_path);
    }

    cur_node.children.map((x) => buf.push([x, cur_path]));
  }

  return results;
}

export function findParent(
  rootNode: SenseTreeNode, target: SenseTreeNode): SenseTreeNode | null {
  let paths = findNodeTrace(rootNode, (x) => x == target);
  if (paths.length && paths[0].length > 1) {
    return paths[0][paths[0].length - 2];
  } else {
    return null;
  }
}

export function moveNode(
  root: SenseTreeNode,
  srcNode: SenseTreeNode,
  tgtNode: SenseTreeNode): SenseTreeNode {

  let newRoot = Object.assign({}, root);
  const tgtParent = findParent(root, tgtNode)
  const srcParent = findParent(root, srcNode);

  // debugger;
  if (tgtNode.node_type === "group") {
    // it is a group node
    if (srcParent) {
      // remove srcNode from its parent
      const srcIndex = srcParent.children.indexOf(srcNode);
      srcParent.children.splice(srcIndex, 1);

      tgtNode.children.push(srcNode);
    }
  } else {
    // it is a leaf(sense) node    

    if (tgtParent && srcParent) {
      // remove srcNode from its parent      
      const srcIndex = srcParent.children.indexOf(srcNode);
      srcParent.children.splice(srcIndex, 1);

      // add srcNode to tgtParent
      const nodeList = tgtParent.children;
      const tgtIndex = nodeList.indexOf(tgtNode);
      let newNodeList = [
        ...nodeList.slice(0, tgtIndex + 1),
        srcNode,
        ...nodeList.slice(tgtIndex + 1, nodeList.length)]
      tgtParent.children = newNodeList;
    }

  }


  return newRoot;
}

type LeafTuple = { node: SenseTreeNode, parent: SenseTreeNode | null };
export function getLeaves(
  node: SenseTreeNode, parent: SenseTreeNode | null = null):
  LeafTuple[] {
  if (node.children.length > 0) {
    return node.children.map((x) => getLeaves(x, node)).flat();
  } else {
    return [{ node, parent }];
  }
}

export function getGroupedSenses(
  node: SenseTreeNode,
  ref_senses: CwnSense[],
  filterText?: string): GroupedSenses {

  type SenseTuple = { sense: CwnSense, parent: SenseTreeNode | null };  
  const leaf_nodes = getLeaves(node);

  const sense_nodes = leaf_nodes
    .map((x) => {
      const idx = ref_senses
        .map((x) => x.cwn_id)
        .indexOf(x.node.node_id)
      let sense_x = null;
      if (idx >= 0) {
        sense_x = ref_senses[idx];
      }
      return { sense: sense_x, parent: x.parent } as SenseTuple;
    })
    .filter((x) => !!x.sense)
    .filter((x) => !filterText || x.sense.cwn_id.indexOf(filterText) >= 0);

  const items = sense_nodes.map((x) => x.sense);
  const parents = sense_nodes.map((x) => x.parent);
  const group_map = items.reduce((obj, item_x, i) => {
    const parent_x = parents[i];
    const parent_id = parent_x ? parent_x.node_id : "__null__";
    obj[parent_id] = [...obj[parent_id] || [], item_x];
    return obj;
  }, {} as { [key: string]: CwnSense[] })

  const out_items = Object.values(group_map).flat();
  const out_groups = Object.keys(group_map).map((node_id: string) => {
    let values = group_map[node_id];
    let groupNode = findNode(node, (x) => x.node_id === node_id);
    let groupTitle = (groupNode?.title) ? (groupNode?.title) : node_id;

    return {
      count: values.length,
      startIndex: out_items.indexOf(values[0]),
      key: node_id,
      name: groupTitle,
      data: parents.filter((x) => x && (x.node_id == node_id))[0],
      level: 0
    } as IGroup
  });

  return { senses: out_items, groups: out_groups } as GroupedSenses;
}

export function makeSenseTreeGroupNode(title: string = "") {
  return {
    node_id: `group-${Date.now()}`,
    description: "",
    node_type: "group",
    title: title.length > 0 ? title : "New Group",
    tag: "",
    children: []
  } as SenseTreeNode;
}

export function makeSenseTreeSenseNode(sense: CwnSense) {
  return {
    node_id: sense.cwn_id,
    description: "",
    node_type: "sense",
    title: `(${sense.pos})${sense.definition}`,
    tag: "",
    children: []
  } as SenseTreeNode;
}

export function checkSenseTree(senseTree: SenseTree, senses: CwnSense[]) {
  const root = senseTree.root;
  const senseNodeIds = getLeaves(root)
    .filter((x) => x.node.node_type == "sense")
    .map((x) => x.node.node_id);
  
  let orphanSenses = senses.filter((x) => senseNodeIds.indexOf(x.cwn_id) < 0)
  if (orphanSenses.length > 0) {
    let orphanGroup = makeSenseTreeGroupNode("");
    orphanGroup.children = orphanSenses.map(makeSenseTreeSenseNode);
    root.children.push(orphanGroup);
  }

  return senseTree;
}

export function removeGroupNode(root: SenseTreeNode, node: SenseTreeNode) {
  const children = node.children;
  const parent = findParent(root, node);
  if (parent) {
    root.children.push(...children);
    parent.children = parent.children.filter((x) => x !== node);
  }
  return root;

}

export function addGroupNode(root: SenseTreeNode) {
  let newNode = makeSenseTreeGroupNode();
  root.children.unshift(newNode);
  return root;
}