import React from 'react';
import { CwnSense } from "../datatypes";
import { CompactPeoplePicker, IBasePickerProps, IPersonaProps, PersonaInitialsColor } from "@fluentui/react";

export interface ISensePickerProps {
  senseCandidates: CwnSense[];  
  onSenseSuggestions: (filter: string) => CwnSense[];
  onPickerChanged: (senseIds: string[]) => void;
  onSubmit: () => void;
}

export function SensePicker(props: ISensePickerProps) {
  function onFilterChanged(filter: string) {
    let senses = props.onSenseSuggestions(filter);
    return senses.map((sense, index) => {
      return {
        imageInitials: sense.pos,
        initialsColor: (index % 6) as PersonaInitialsColor,
        text: sense.definition,
        optionalText: sense.cwn_id
      }
    });
  }

  const onSelectedItemsChange = (items?: IPersonaProps[]) => {
    if (items) {
      props.onPickerChanged(items.map((x) => {
        return x.optionalText?x.optionalText:""
      }));
    }
  }

  let selectedItems = props.senseCandidates.map((sense) => {
    return {
      imageInitials: sense.pos,
      initialsColor: PersonaInitialsColor[0],
      text: sense.definition,
      optionalText: sense.cwn_id,
      styles: {primaryText: {fontSize: "8pt"}}
    }
  });

  return (
    <CompactPeoplePicker
      selectedItems={selectedItems}
      onResolveSuggestions={onFilterChanged}
      onChange={onSelectedItemsChange}  
      
    />
  )
}