import React from 'react';
import { User, SenseAnnotSession, Assignments, Annotations, Assignment } from "../datatypes"
import { Stack } from '@fluentui/react';
import { LoginStatus } from './LoginStatus';
import { SessionInfo } from './SessionInfo';
import { Tasks } from './Tasks';
import { SessionDispatch } from '../SenseAnnot/sessionUtils/sessionReducer';

export interface IControllerData {
  user: User | null;
  session: SenseAnnotSession | null;
  assignments: Assignments;
  annotations: Annotations;
  sessionDispatch: SessionDispatch
  login_fn: () => void;
  logout_fn: () => void;
}

function Controller(props: IControllerData) {

  return (

    <Stack tokens={{ childrenGap: 5, padding: 10 }}>
      <LoginStatus user={props.user} login_fn={props.login_fn} logout_fn={props.logout_fn} />
      <SessionInfo session={props.session}></SessionInfo>
      <Tasks
        session={props.session}
        assignments={props.assignments}
        annotations={props.annotations}
        sessionDispatch={props.sessionDispatch}
        ></Tasks>
    </Stack>
  )
}


export default Controller;