import React from 'react';
import { PretaggedToken } from '../datatypes';
import { Stack, mergeStyles, IStyle } from '@fluentui/react';
import './token.css';

export interface ITokenProps {
  token: PretaggedToken;
  isFocus?: boolean;
  simplified?: boolean;
}

export function Token(props: ITokenProps) {
  let { token, isFocus } = props;  
  let posStyle = { fontSize: "10pt" };

  let pos = props.token[1].endsWith("CATEGORY")?
    "\u3000":props.token[1];

  return (
    <Stack horizontalAlign="center" className={isFocus ? "focus-token" : ""}>      
      {/* POS */}
      <div style={posStyle}>{props.simplified? "\u3000": pos}</div>
      <div
        style={{fontSize: "large", whiteSpace: "nowrap"}}
        title={`${token[2]}:${token[3]}`}>
        {token[0]}
      </div> {/* Word */}
    </Stack>
  )
}