import { SenseAnnotSession, User, Assignments, Annotations, PretaggedSentence, PretaggedToken, SenseTree, SenseRevision, Annotation, Assignment } from "../datatypes";
import { CwnSense } from "../datatypes";
import { object } from "prop-types";
import firebase from "firebase";
import { makeNewSenseTree } from "../SenseSorter/sorter_utils";



export async function fetchSenses(lemma: string): Promise<CwnSense[]> {
  let db = firebase.firestore();
  let doc = await db.collection("senses").doc(lemma).get();

  if (doc.exists) {
    let data = doc.data() as {lemma: string, senses: CwnSense[]};
    return data.senses;
  } else {
    throw new Error("Cannot find lemma " + lemma);
  }
}


export async function fetchSession(user: User)
  : Promise<SenseAnnotSession | null> {
  let db = firebase.firestore();    
  let doc = await db.collection("sessions").doc(user.uid).get();
  if (doc.exists) {
    return doc.data() as SenseAnnotSession;
  } else {
    return null;
  }
}


export async function fetchAssignments(user: User)
  : Promise<Assignments> {
  let db = firebase.firestore();
  let query = await db.collection("assignments")
    .where("annotatorId", "==", user.uid).get();
  let assignments: Assignments = []
  query.forEach((doc) => {
    assignments.push(doc.data() as Assignment);
  });
  return assignments;
}

export async function fetchAnnotations(user: User)
  : Promise<Annotations> {
  let db = firebase.firestore();
  let query = await db.collection("annotations")
    .where("annotatorId", "==", user.uid).get();
  let annotations: Annotations = {};
  query.forEach((doc) => {
    let annot = doc.data() as Annotation;
    annotations[annot.assignId] = annot;
  });
  return annotations;
}


export async function fetchSentence(sentenceId: string)
  : Promise<PretaggedToken[]> {
  let db = firebase.firestore();

  let doc = await db.collection("sentences").doc(sentenceId).get();
  if (doc.exists) {
    let data = doc.data()! as { sid: string, tokens: string[] };
    let tokens = (data.tokens as string[])
      .map((x) => x.split("::")) as PretaggedToken[];
    return tokens;
  } else {
    return [];
  }
}

export async function fetchSenseTree(userEmail: string, lemma: string)
  : Promise<SenseTree | null> {

  let db = firebase.firestore();
  let treeId = `${userEmail}-${lemma}`;
  let senseTree = null;

  let doc = await db.collection("senseTrees").doc(treeId).get();
  if (doc.exists) {
    senseTree = doc.data() as SenseTree;
  }

  return senseTree;
}
