import { Assignments, Annotations, SenseAnnotSession, Assignment, SenseRevision } from "../../datatypes";
import { SessionActionPayload } from "./sessionReducer";

export class SessionMapper {
  private session = {} as SenseAnnotSession;
  private assignments = [] as Assignments;
  private annotations = {} as Annotations;

  constructor(session: SenseAnnotSession,
    assignments: Assignments,
    annotations: Annotations) {
    this.session = session;
    this.assignments = assignments;
    this.annotations = annotations;
  }

  nextTrial() { return this.stepTrial((x) => x + 1); }

  prevTrial() { return this.stepTrial((x) => x - 1); }

  nextTodo() { return this.stepTodo((x) => x + 1); }

  prevTodo() { return this.stepTodo((x) => x - 1); }

  nextAmbiguous() { return this.stepAmbig((x) => x + 1); }

  prevAmbiguous() { return this.stepAmbig((x) => x - 1); }

  initializeSession() {    
    if (this.hasAssignments()) {
      return this.gotoAssignment(this.assignments[0].assignId);
    } else {
      return this.session;
    }
  }

  gotoAssignment(newAssignId: string) {
    try {
      const assig = this.findAssignment(newAssignId);
      if (assig.items.length > 0) {
        let newSession = this.cloneSession(
          assig.assignId, assig.items[0]);
        return newSession;
      } else {
        return this.session;
      }
    } catch (err) {
      console.error(err);
      return this.session;
    }
  }

  gotoItemNo(itemNo: number){
    let newSession = this.session;
    try{
      const assig = this.findAssignment(this.session.currentAssignmentId);
      if(itemNo >= 0 && itemNo < assig.items.length){
        const itemId = assig.items[itemNo];
        newSession = this.cloneSession(assig.assignId, itemId);        
      } 
    } catch (err) {
      console.error("Error when gotoItemNo")
      console.error(err);
    }
    return newSession;
  }

  private hasAssignments() {
    return this.assignments.length > 0
  }

  private stepTrial(stepFunc: (x: number) => number) {
    let newSession = this.session;
    try {
      const assig = this.findAssignment(this.session.currentAssignmentId);
      let itemNo = this.findItemNo(assig, this.session.currentItemId);
      itemNo = stepFunc(itemNo);
      if (itemNo >= 0 && itemNo < assig.items.length) {
        const newItemId = assig.items[itemNo];
        newSession = this.cloneSession(assig.assignId, newItemId);
      }

    } catch (err) {
      console.error(err);
    }
    
    return newSession;
  }

  private stepTodo(stepFunc: (x: number) => number) {
    let newSession = this.session;
    try {
      const assig = this.findAssignment(this.session.currentAssignmentId);
      const curItemId = this.session.currentItemId;      
      const dones = assig.assignId in this.annotations?
        Object.keys(this.annotations[assig.assignId].results): [];

      const todos = assig.items.filter((x) => x == curItemId || dones.indexOf(x) < 0);
      let cursor = stepFunc(todos.indexOf(curItemId));      
      if (cursor > 0 && cursor < todos.length) {
        const newItemId = todos[cursor];
        newSession = this.cloneSession(assig.assignId, newItemId);
      }
    } catch (err) {
      console.error(err);
    }

    return newSession;
  }

  private stepAmbig(stepFunc: (x: number) => number) {
    let newSession = this.session;

    try {
      const assig = this.findAssignment(this.session.currentAssignmentId);
      const curItemId = this.session.currentItemId;
      const results = assig.assignId in this.annotations?
        this.annotations[assig.assignId].results: {};
      const ambigs = assig.items.filter((itemId: string) => {
        if(!(itemId in results)) return false;
        const rev = results[itemId];
        return rev.probs.length > 0 && rev.probs[0] > 0.5;        
      });

      let cursor = stepFunc(ambigs.indexOf(curItemId));
      if (cursor > 0 && cursor < ambigs.length) {
        const newItemId = ambigs[cursor];
        newSession = this.cloneSession(assig.assignId, newItemId);
      }
    } catch (err) {
      console.error(err);
    }

    return newSession;
  }

  private findAssignment(assignId: string): Assignment {    
    const curAssigs = this.assignments.filter((x) => x.assignId == assignId);

    if (curAssigs.length > 0) {
      return curAssigs[0];
    } else {
      throw new Error(`Cannot find current assignment (${assignId})`)
    }
  }

  private findItemNo(
    assignment: Assignment, itemId: string): number {

    const curItemId = itemId !== undefined? itemId: this.session.currentItemId    
    const curItemSerial = assignment.items.indexOf(curItemId);

    if (curItemSerial >= 0) {
      return curItemSerial;
    } else {
      throw Error(`itemId (${curItemId}) not found in assignment(${assignment.assignId})`);
    }

  }

  private cloneSession(assignId: string = "", itemId: string = "") {

    let newSession = Object.assign({}, this.session);
    if (assignId.length > 0) {
      newSession.currentAssignmentId = assignId;
    }

    if (itemId.length > 0) {
      newSession.currentItemId = itemId;
    }

    try{
      const assig = this.findAssignment(assignId);
      newSession.currentItemNo = this.findItemNo(assig, itemId);
    } catch (err){
      console.error(err);
    }

    newSession.lastActivity = Date.now();
    
    return newSession;
  }

}