import { SenseAnnotSession, Annotations, Annotation } from "../datatypes";
import firebase from "firebase";

export function uploadSession(session: SenseAnnotSession){  
  let db = firebase.firestore();
  let userId = session.userId;
  let userEmail = session.userEmail;
  let result = db.collection("sessions").doc(userId).set(session);
  return result;
}

export function uploadAnnotation(annotation: Annotation){
    let db = firebase.firestore();
    let result = db.collection("annotations")
      .doc(annotation.annotId).set(annotation);
    return result;
}