import React from 'react';
import { Stack, IconButton, TextField, Label } from '@fluentui/react';
import { SessionDispatch, SessionActionType as SAT } from '../../SenseAnnot/sessionUtils/sessionReducer';

export interface INavigationBar {
  currentItemNo: number;
  sessionDispatch: SessionDispatch;
}
export function NavigationBar(props: INavigationBar) {
  let buttonStyle = {
    root:
      { color: "white", border: "1px solid white", borderRadius: "5px" }
  }

  const handler = makeEventHandler(props.sessionDispatch)
  const currentItemNo = (props.currentItemNo + 1).toString();

  return (
    <>
      <Stack horizontal horizontalAlign="space-evenly">
        <IconButton styles={buttonStyle}
          onClick={handler.onPrevTrial}
          iconProps={{ iconName: "CaretSolidLeft" }}
          title="Previous Item" />
        <IconButton styles={buttonStyle}
          onClick={handler.onPrevTodo}
          iconProps={{ iconName: "CaretLeft8" }}
          title="Previous Todo" />
        <IconButton styles={buttonStyle}
          onClick={handler.onNextTodo}
          iconProps={{ iconName: "CaretRight8" }} title="Next Todo" />
        <IconButton styles={buttonStyle}
          onClick={handler.onNextTrial}
          iconProps={{ iconName: "CaretSolidRight" }}
          title="Next Item" />
      </Stack>
      <Stack horizontal>
        <Stack.Item>
          <Label styles={{ root: { color: "white", minWidth: "70px" } }}>Go to Item</Label>
        </Stack.Item>
        <Stack.Item grow={1}>
          <TextField styles={{ root: { width: "100%" } }}
            key={currentItemNo}
            defaultValue={currentItemNo}
            onKeyPress={(event) => {
              if (event.key === 'Enter') {
                const target = event.target as HTMLTextAreaElement;
                const newValue = parseInt(target.value);
                if (newValue != undefined && !isNaN(newValue)) {
                  handler.onItemNoChanged(newValue-1);
                }
              }
            }} />
        </Stack.Item>
      </Stack>
    </>
  );
}


function makeEventHandler(dispatch: SessionDispatch) {
  return {
    onItemNoChanged: (itemNo: number) => {      
        dispatch(SAT.GotoItemNo, { newItemNo: itemNo });      
    },
    onPrevTrial: () => { dispatch(SAT.PrevTrial); },
    onNextTrial: () => { dispatch(SAT.NextTrial); },
    onPrevTodo: () => { dispatch(SAT.PrevTodo); },
    onNextTodo: () => { dispatch(SAT.NextTodo); },
    onPrevAmbig: () => { dispatch(SAT.PrevAmbig); },
    onNextAmbig: () => { dispatch(SAT.NextAmbig); }
  }
}
