import * as firebase from "firebase/app";
import "firebase/auth";
import {User} from "../datatypes"

function transformUser(user_raw: firebase.User): User{
  let empty = (x: string|null) => {return x? x: ""};
  let user = {
    name: empty(user_raw.displayName),
    email: empty(user_raw.email),
    photoUrl: empty(user_raw.photoURL),
    uid: empty(user_raw.uid)
  }
  return user;
}
export async function login() {
  console.log("login function called");
  let provider = new firebase.auth.GoogleAuthProvider();
  try {
    const result = await firebase.auth().setPersistence(firebase.auth.Auth.Persistence.LOCAL)
                    .then(()=>{
                      return firebase.auth().signInWithPopup(provider);
                    });
    
    if (result.credential) {
      let token = (result.credential as firebase.auth.OAuthCredential).accessToken;
      let user: User|null = null;      
      let user_raw = result.user;
      if (user_raw){        
        user = transformUser(user_raw);
      }
      return user;      
    }
  }
  catch (error) {
    console.debug(error);
  }
}

export function logout() {
  firebase.auth().signOut();
}

export function subscribeLoginStatus(
    signed_in: ((user: User)=>void), 
    not_signed_in: (()=>void)) {
  firebase.auth().onAuthStateChanged((raw_user)=>{
    if(raw_user){
      let user = transformUser(raw_user);
      signed_in(user);
    } else {
      not_signed_in();
    }
  })
}