import { User, PretaggedToken, SenseRevision, CwnSense, SenseTree, Trial, Annotations } from "../datatypes";
import { fetchAnnotations, fetchSenses } from "../DataHandler";
import { fetchSenseTree } from "../DataHandler/getData";
import { makeNewSenseTree } from "./sorter_utils";

type StateSetter<T> = React.Dispatch<React.SetStateAction<T>>

export async function updateSenses(
  trial: Trial,
  senseSetter: StateSetter<CwnSense[]>)
  : Promise<CwnSense[]> {

  let token = trial.token;
  let senses = [] as CwnSense[];
  if (token) {
    let lemma = token[0];
    try {
      senses = await fetchSenses(lemma);
    } catch (err) {
      console.error(err);
    }
  }
  senseSetter(senses);
  return senses;
}

export async function initSenseTree(
  trial: Trial) {


  let senseTree: SenseTree | null = null;
  if (trial && trial.token) {
    const userEmail = trial.userEmail;
    const token = trial.token;
    const lemma = token[0];

    try {
      senseTree = await fetchSenseTree(userEmail, lemma);
    } catch (err) {
      // console.error(err);
    }

    if (senseTree === null) {
      senseTree = makeNewSenseTree(trial);
    }
  }
  return senseTree;
}

