import React, { useReducer, useEffect } from "react";
import { SenseAnnotSession, Assignments, Annotations, User } from "../../datatypes";
import { SessionMapper } from "./sessionHandler";
import { uploadSession } from "../annot_uploader";

export interface SessionAction {
  type: SessionActionType;
  payload: SessionActionPayload;
}

export interface SessionActionPayload {
  newSession?: NullableSessionType;
  newAssignId?: string;
  newItemId?: string;
  newItemNo?: number;
}

export enum SessionActionType {
  Update = "UPDATE",
  Init = "INIT",
  GotoAssignment = "GOTO_ASSIGNMENT",
  GotoItemNo = "GOTO_ITEMNO",
  NextTrial = "NEXT_TRIAL",
  PrevTrial = "PREV_TRIAL",
  NextTodo = "NEXT_TODO",
  PrevTodo = "PREV_TODO",
  NextAmbig = "NEXT_AMBIG",
  PrevAmbig = "PREV_AMBIG"
}

type NullableSessionType = SenseAnnotSession | null;
export type SessionDispatch = (type: SessionActionType, payload?: SessionActionPayload) => void;

export function useSession(
  assignments: Assignments,
  annotations: Annotations
): [NullableSessionType, SessionDispatch] {

  let initState: NullableSessionType = null;

  function reducer(session: NullableSessionType, action: SessionAction): NullableSessionType {

    let newSession = session;
    if (session === null) {
      if (action.type == SessionActionType.Update) {
        const payload = action.payload;
        newSession = payload.newSession ? payload.newSession : session;
      }
    } else {
      newSession = mapSession(session, assignments, annotations, action);
    }

    return newSession;
  }

  const [session, dispatch] = useReducer(reducer, initState);

  useEffect(()=>{    
    if (session) {     
      console.log("Upload new session");       
      uploadSession(session);
    }
  }, [session]);

  function dispatch_wrapper(type: SessionActionType,
    payload: SessionActionPayload = {}) {

    dispatch({
      type: type,
      payload: payload
    });
  }

  return [session, dispatch_wrapper];
}

function mapSession(
  session: SenseAnnotSession,
  assignments: Assignments,
  annotations: Annotations,
  action: SessionAction) {
  
  const handler = new SessionMapper(session, assignments, annotations);
  let newSession = null;
  let payload = action.payload;
  switch (action.type) {
    case SessionActionType.Update:
      newSession = payload.newSession ? payload.newSession : session;
    case SessionActionType.Init:
      newSession = handler.initializeSession();
      break;
    case SessionActionType.GotoAssignment:
      newSession = handler.gotoAssignment(payload.newAssignId!);
      break;
      case SessionActionType.GotoItemNo:
        newSession = handler.gotoItemNo(payload.newItemNo!);
        break;
    case SessionActionType.NextTrial:
      newSession = handler.nextTrial();
      break;
    case SessionActionType.PrevTrial:
      newSession = handler.prevTrial();
      break;
    case SessionActionType.NextTodo:
      newSession = handler.nextTodo();
      break;
    case SessionActionType.PrevTodo:      
      newSession = handler.prevTodo();
      break;
    case SessionActionType.NextAmbig:
      newSession = handler.nextAmbiguous();
      break;
    case SessionActionType.PrevAmbig:
      newSession = handler.prevAmbiguous();
      break;
    default:
      console.error("Unsupported session action type");
      break;
  }

  return newSession;
}

