import { useDrop, useDrag } from "react-dnd";
import React, { useRef, useState } from "react";
import { Link, IGroupHeaderProps, IconButton, Stack, IGroup } from "@fluentui/react";
import { SenseDragItem } from "./SenseItem";
import { GroupInfoEdit, IGroupInfo } from "./GroupInfoEdit";

interface GroupDragItem {
  type: "group-node";
  nodeId: string;
}

export interface GroupHeaderDndHandler {
  onDragAndDrop: (srcNodeId: string, tgtNodeId: string) => void;
  onGroupHeaderUpdate: (groupInfo: IGroupInfo) => void;
  onDeleteGroup: (group: IGroup) => void;
  deleteState: boolean;
}

export function SenseTreeGroupHeader(props: IGroupHeaderProps & GroupHeaderDndHandler) {
  const toggleCollapse = (): void => {
    props.onToggleCollapse!(props.group!);
  };

  const [editVisibility, setEditVisibility] = useState(false);
  const toggleEditVisibility = () => {
    setEditVisibility(!editVisibility);
  }  

  const ref = useRef<HTMLDivElement>(null);
  const [{ isOver, canDrop }, drop] = useDrop({
    accept: ["group-node", "leaf-node"],
    drop: (item: GroupDragItem | SenseDragItem, monitor) => {
      let groupNodeId = props.group!.key;
      if (item.type == "group-node") {
        props.onDragAndDrop(item.nodeId, groupNodeId);
      } else {
        props.onDragAndDrop(item.senseId, groupNodeId);
      }

    },
    canDrop: (item, monitor) => {
      if (item.type == "leaf-node"){
        return true;
      } else {
        return item.nodeId != props.group!.key;
      }
      
    },
  });

  const [{ isDragging }, drag, preview] = useDrag({
    item: { type: "group-node", nodeId: props.group!.key },
    collect: (monitor) => ({
      isDragging: !!monitor.isDragging(),
    }),
  });

  if (props.group) {
    drag(drop(ref));

    const groupInfo = { title: props.group.name, tag: props.group.data.tag }
    const tag = groupInfo.tag;
    let groupButton = null;
    if (props.group.isCollapsed) {
      groupButton = (<IconButton onClick={toggleCollapse} iconProps={{ iconName: "ChevronDown" }} title="" />)
    } else {
      groupButton = (<IconButton onClick={toggleCollapse} iconProps={{ iconName: "ChevronRight" }} title="" />)
    }

    let allCount = props.group.data.allCount;
    let countStr = allCount || allCount === 0 ? `(${props.group.data.allCount})` : "";
    let deleteButton = props.deleteState ? (
      <IconButton iconProps={{ iconName: "Delete" }}
        onClick={()=>props.onDeleteGroup(props.group!)} />
      ) : null;

    let editCallout = editVisibility ? (
      <GroupInfoEdit
        elemId={`group-header-${props.group.key}`}
        groupInfo={groupInfo}
        onDismiss={toggleEditVisibility}
        onGroupInfoUpdate={props.onGroupHeaderUpdate}></GroupInfoEdit>) : null;

    return (
      <div ref={ref} className="d-flex p-1 align-items-baseline user-select-none"
        style={{ marginLeft: props.groupLevel! * 20 }}>
        {editCallout}
        {groupButton}
        <Stack
          id={`group-header-${props.group.key}`}
          horizontal
          verticalAlign="baseline"
          horizontalAlign="space-between"
          onClick={() => props.onGroupHeaderClick!(props.group!)}
          onDoubleClick={() => toggleEditVisibility()}>
          {deleteButton}
          {(tag && tag.length > 0)?
            <h6><span className="badge bg-primary">{tag}</span>&nbsp;</h6>:null }
          <h6>{props.group.name} {countStr}</h6>
        </Stack>
      </div>
    );
  } else {
    return (<div></div>)
  }
}