import React, { useState } from "react";
import { FocusTrapCallout, Stack, Label, TextField, mergeStyleSets, Button, PrimaryButton } from "@fluentui/react";

export interface IGroupInfo {
  title: string;
  tag: string;
}

export interface IGroupInfoEditProps {
  elemId: string;
  groupInfo: IGroupInfo;
  onGroupInfoUpdate: (info: IGroupInfo) => void;
  onDismiss: () => void;
}

export function GroupInfoEdit(props: IGroupInfoEditProps) {
  const [groupInfo, setGroupInfo] = useState(props.groupInfo);

  const styles = mergeStyleSets({
    root: { padding: "5%" },
    label: { width: "60px", margin: "1% 0%" },
    textInput: {}
  })

  const onSave = () => {
    setGroupInfo(groupInfo);
    props.onGroupInfoUpdate(groupInfo);
    props.onDismiss();
  }

  return (
    <FocusTrapCallout
      role="alertdialog"
      gapSpace={0}
      target={`#${props.elemId}`}
      onDismiss={props.onDismiss}
      setInitialFocus
    >
      <Stack className={styles.root}>
        <Stack horizontal>
          <Label className={styles.label}>Title</Label>
          <TextField className={styles.textInput}
            onChange={(ev, newvalue) => setGroupInfo({ ...groupInfo, title: newvalue ? newvalue : "" })}
            defaultValue={groupInfo.title}></TextField>
        </Stack>
        <Stack horizontal>
          <Label className={styles.label}>Tag</Label>
          <TextField className={styles.textInput}
            onChange={(ev, newvalue) => setGroupInfo({ ...groupInfo, tag: newvalue ? newvalue : "" })}
            defaultValue={groupInfo.tag}></TextField>
        </Stack>
        <Stack horizontal horizontalAlign="end" tokens={{ childrenGap: "10px" }}
          styles={{ root: { paddingTop: "16px" } }}>
          <Button onClick={props.onDismiss}>Cancel</Button>
          <PrimaryButton onClick={onSave}>Save</PrimaryButton>
        </Stack>
      </Stack>

    </FocusTrapCallout>
  )
}

