import React, { useEffect, useState } from 'react';
import Controller from '../Controller/Controller';
import SentenceViewer from '../SentenceViewer/SentenceViewer';
import SenseSorter from '../SenseSorter/SenseSorter';
import { Assignments, Trial, Annotations, SenseRevision, Annotation } from "../datatypes";
import { User, SenseAnnotSession } from "../datatypes";
import { updateSession, updateAssignments, updateTrial, updateAnnotations, makeNewAnnotation } from "./annot_updater";
import { login, logout, subscribeLoginStatus } from '../DataHandler';
import * as firebase from "firebase/app";
import "firebase/auth";
import "firebase/analytics";
import { uploadAnnotation } from './annot_uploader';
import { useSession } from './sessionUtils';
import { SessionActionType, SessionActionPayload } from './sessionUtils/sessionReducer';

function initializeFirebase() {
  var firebaseConfig = {
    apiKey: "AIzaSyCtaBIZ-IiQ0a5Z0LsuEnVGmojJbY9yvMs",
    authDomain: "langon-281613.firebaseapp.com",
    databaseURL: "https://langon-281613.firebaseio.com",
    projectId: "langon-281613",
    storageBucket: "langon-281613.appspot.com",
    messagingSenderId: "98385288306",
    appId: "1:98385288306:web:09cdd581f06c0e3cdb76eb",
    measurementId: "G-36C7NTBFN0"
  };
  // Initialize Firebase
  firebase.initializeApp(firebaseConfig);
  firebase.analytics();
}

function SenseAnnot() {
  const [user, setUser] = useState<User | null>(null);
  const [annotations, setAnnotations] = useState({} as Annotations);
  const [assignments, setAssignments] = useState<Assignments>([]);
  const [session, sessionDispatch] = useSession(assignments, annotations);
  const [trial, setTrial] = useState<Trial>({} as Trial);

  console.log("rendering SenseAnnot");
  
  // initialization
  useEffect(() => {
    initializeFirebase();
    subscribeLoginStatus(
      (user) => setUser(user),
      () => setUser(null));
  }, []);

  // update session, depends on user
  useEffect(() => { updateSession(user, sessionDispatch) }, [user]);
  // update Assignments, depends on user
  useEffect(() => { updateAssignments(user, setAssignments) }, [user])
  // update annotations, depends on user
  useEffect(() => { updateAnnotations(user, setAnnotations) }, [user]);
  // update trial, depends on session
  useEffect(() => { updateTrial(session, setTrial) }, [session, assignments]);

  // set session to default value
  useEffect(() => {
    if (session && session.currentAssignmentId.length == 0) {      
      sessionDispatch(SessionActionType.Init);
    }
  }, [assignments]);

  let currentItemId = session ? session.currentItemId : "";

  let controllerStyle = {
    background: "radial-gradient(circle at bottom left, rgba(0,0,0,1) 0%, rgba(75,75,75,1) 100%)",
    borderRight: "2px dashed darkblue",
    color: "white",
  }

  /*
   *   Event callback
   */
  const updateSenseRevision = (
    session: SenseAnnotSession | null,
    trial: Trial,
    revision: SenseRevision | null = null) => {

    if (session && revision) {
      const assignId = trial.assignId;
      const itemId = trial.itemId;
      let newAnnotations = Object.assign({}, annotations);
      let annot = newAnnotations[assignId];

      if (annot) {
        annot.results[itemId] = revision;
      } else {
        annot = makeNewAnnotation(assignId, session);
        annot.results[itemId] = revision;
        newAnnotations[assignId] = annot;
      }
      
      setAnnotations(newAnnotations);      
      onAnnotationUpdated(annot);
    }
  }


  /*
   *    Database update callback
   */
  const onAnnotationUpdated = (newAnnotation: Annotation) => {
    uploadAnnotation(newAnnotation);
  }

  return (
    <div className="container">
      <div className="row">
        <div className="col-md-3 col-sm-6 d-none d-sm-block" style={controllerStyle}>
          <Controller
            user={user}
            session={session}
            assignments={assignments}
            annotations={annotations}
            sessionDispatch={sessionDispatch}
            login_fn={login}
            logout_fn={logout}></Controller>
        </div>
        <div className="col-md-9 col-sm-6 col-xs-12">
          <SentenceViewer
            itemId={currentItemId}
            sentence={trial.sentence}
            preContext={trial.preContext}
            postContext={trial.postContext}></SentenceViewer>
          <SenseSorter trial={trial}
            annotations={annotations}
            sessionDispatch={sessionDispatch}
            onSenseRevisionUpdated={(trial, revision) =>
              updateSenseRevision(session, trial, revision)}></SenseSorter>
        </div>
      </div>
    </div>
  )
}

export default SenseAnnot;
