import React, { useState } from 'react';
import { CwnSense, SenseTree, SenseTreePath, SenseRevision } from '../datatypes';
import { Selection, SelectionMode, List, Persona, PersonaSize, PersonaInitialsColor, TooltipHost, DirectionalHint, TooltipDelay, IGroup, GroupedList, Breadcrumb, Stack, TextField } from '@fluentui/react';
import { GroupedSenses, findNode, moveNode, findNodes } from './tree_utils';
import SenseItem from './SenseItem';
import { cloneSenseRevision } from './sorter_utils';

export interface ISenseListProps {
  senseTree: SenseTree | null;
  groupedSenses: GroupedSenses;
  senseRevision: SenseRevision;
  onSenseTreeUpdated: (tree: SenseTree) => void;
  onSenseRevisionUpdated: (revision: SenseRevision) => void;
}

let selectionModel = new Selection({
  onSelectionChanged: () => {
    console.log(selectionModel.getSelection());
  }
});


export function SenseList(props: ISenseListProps) {
  let senses: CwnSense[] = [];
  let groups: IGroup[] = [];
  const [senseFilter, setSenseFilter] = useState<string | null>(null);

  if (Object.keys(props.groupedSenses).length > 0) {
    senses = props.groupedSenses.senses;
    groups = props.groupedSenses.groups;
  }

  function onSenseDragAndDrop(srcSenseId: string, tgtSenseId: string) {
    const senseTree = props.senseTree;
    if (senseTree) {
      const srcNode = findNode(senseTree.root, (x) => x.node_id == srcSenseId);
      const tgtNode = findNode(senseTree.root, (x) => x.node_id == tgtSenseId);

      if (srcNode && tgtNode) {
        const newRoot = moveNode(senseTree.root, srcNode, tgtNode);
        const newTree = Object.assign({}, senseTree);
        newTree.timestamp = Date.now();
        newTree.root = newRoot;
        props.onSenseTreeUpdated(newTree);
      }
    }
  }


  const onSenseSelected = (sense: CwnSense) => {
    const senseRevision = props.senseRevision;

    console.log("onSenseSelected (senseRevision): ", senseRevision);
    let candidates = senseRevision.candidates;
    if (senseRevision.candidates.indexOf(sense.cwn_id) < 0) {
      candidates.push(sense.cwn_id);
      const newRevision = cloneSenseRevision(senseRevision,
        candidates = candidates);
      props.onSenseRevisionUpdated(newRevision);
    }
  }

  function renderCell(nestingDepth?: number,
    sense?: CwnSense, index?: number) {

    if (sense && index !== undefined) {


      return (
        <SenseItem
          sense={sense} index={index}
          onDragAndDrop={onSenseDragAndDrop}
          onClick={() => onSenseSelected(sense)}
        />
      )
    }
  }

  return (
    <Stack>
      <div
        id="unique-sense-list-id"
        ref={null/*drop*/}
        style={{
          height: "500px",
          overflowX: "hidden", overflowY: "scroll"
        }}>
        <GroupedList
          items={senses}
          groups={groups}
          onRenderCell={renderCell}
          compact={true}
          selectionMode={SelectionMode.none}
          groupProps={
            { showEmptyGroups: true }
          }
        />
      </div>
    </Stack>
  )
}