import { SenseRevision, Trial, CwnSense, SenseTree, Annotations } from "../datatypes";
import { findNodes, getLeaves, makeSenseTreeGroupNode } from "./tree_utils";


export function getSenseRevision(
  trial: Trial, annotations: Annotations) {

  const itemId = trial.itemId;
  const assignId = trial.assignId;
  let revision = null;
  
  if (assignId && itemId) {
    let annot = annotations[assignId];
    if (annot && itemId in annot.results) {
      revision = annot.results[itemId];
    }
  }

  if(revision === null){
    revision = makeNewSenseRevision(trial);
  }
  
  return revision;
}

export function makeNewSenseRevision(trial: Trial): SenseRevision {
  const revision = {
    annotator: trial.userEmail,
    timestamp: Date.now(),
    assignId: trial.assignId,
    itemId: trial.itemId,
    candidates: [],
    probs: []
  };

  return revision;
}

export function cloneSenseRevision(
  revision: SenseRevision,
  candidates?: string[], probs?: number[]): SenseRevision {
  let newRevision = Object.assign({}, revision);
  newRevision.timestamp = Date.now();
  if (candidates) {
    newRevision.candidates = candidates;
  }
  if (probs) {
    newRevision.probs = probs;
  } else {
    newRevision.probs = newRevision.candidates
      .map((x) => 1 / (newRevision.candidates.length));
  }
  
  return newRevision;
};


export function makeNewSenseTree(trial: Trial): SenseTree {
  let senseTree = {
    annotator: trial.userEmail,
    annotatorId: trial.userId,
    timestamp: Date.now(),
    lemma: trial.token[0],
    root: makeSenseTreeGroupNode(`${trial.token[0]}`)
  } as SenseTree;

  return senseTree;
}

function findSense(cwn_id: string, senses: CwnSense[]) {
  let index = senses.map((x) => x.cwn_id).indexOf(cwn_id);

  if (index < 0) {
    return null;
  } else {
    return senses[index];
  }
}

export function parseFilter(filter: string, senses: CwnSense[], senseTree: SenseTree | null) {

  const tokens = filter.split(">>");
  const filterStr = tokens[0];

  let serial = -1;
  if (tokens.length > 1) {
    serial = parseInt(tokens[1]) ? parseInt(tokens[1]) : -1;
  }

  let senseInGroups: CwnSense[] = [];
  if (senseTree) {
    //debugger;
    let groups = findNodes(senseTree.root,
      (x) => x.node_type == "group" &&
        ((x.title.indexOf(filterStr) >= 0) || x.tag?.indexOf(filterStr) >= 0));
    senseInGroups = groups.map((x) => getLeaves(x))
      .flatMap((leafs) => leafs
        .filter((leaf) => leaf.node.node_type == "sense")
        .map((leaf) => findSense(leaf.node.node_id, senses)))
      .filter((x) => x !== null) as CwnSense[];
  }

  console.log("senseInGroups", senseInGroups);
  if (senseInGroups.length > 0) {
    return serial >= 0 ? [senseInGroups[serial]] : senseInGroups;
  } else {    
    const filteredSenses = senses.filter((x) => {
      return x.cwn_id.startsWith(filterStr) || x.pos.startsWith(filterStr)
    });    
    console.log("filteredSenses", filteredSenses);
    return filteredSenses;
  }
}

