import { Trial, SenseTree } from "../datatypes";
import firebase from "firebase";

export function uploadSenseTree(trial: Trial, senseTree: SenseTree){
  let db = firebase.firestore();
  let userId = trial.userId;
  let userEmail = trial.userEmail;
  let treeId = `${userEmail}-${senseTree.lemma}`;
  let result = db.collection("senseTrees").doc(treeId).set(senseTree);
  return result;
}