import React, { useState } from 'react';
import { SenseTree, SenseTreeNode } from '../datatypes';
import { Depths } from '@uifabric/fluent-theme/lib/fluent/FluentDepths'
import { GroupedList, SelectionMode, IGroup, IGroupHeaderProps, Stack, IconButton } from '@fluentui/react';
import { toGroup, findNode, moveNode, removeGroupNode, addGroupNode } from './tree_utils';
import { SenseTreeGroupHeader } from './SenseTreeGroupHeader';
import { IGroupInfo } from './GroupInfoEdit';

export interface ISenseTreeViewProps {
  senseTree: SenseTree | null;
  onSenseTreeUpdated: (tree: SenseTree | null) => void;
  onSelectedNodeUpdated: (node: SenseTreeNode | null) => void;
}



export function SenseTreeView(props: ISenseTreeViewProps) {
  let senseTree = props.senseTree;
  const [deleteState, setDeleteState] = useState(false);

  const onRenderCell = (nestingDepth?: number, item?: SenseTreeNode, itemIndex?: number): JSX.Element => {
    return (<div></div>);
  }

  const onGroupHeaderDrangAndDrop = (srcNodeId: string, tgtNodeId: string) => {
    const senseTree = props.senseTree;
    if (senseTree) {
      const srcNode = findNode(senseTree.root, (x) => x.node_id == srcNodeId);
      const tgtNode = findNode(senseTree.root, (x) => x.node_id == tgtNodeId);

      if (srcNode && tgtNode) {
        const newRoot = moveNode(senseTree.root, srcNode, tgtNode);
        const newTree = Object.assign({}, senseTree);
        newTree.timestamp = Date.now();
        newTree.root = newRoot;
        props.onSenseTreeUpdated(newTree);
        console.log(newTree);
      }
    }
  }

  const onRenderHeader = (headerProps?: IGroupHeaderProps): JSX.Element => {
    const onGroupHeaderUpdate = (groupInfo: IGroupInfo) => {
      let groupId = headerProps?.group?.key;
      if (groupId && senseTree) {
        let newTree = Object.assign({}, senseTree)
        let groupNode = findNode(newTree.root, (x) => x.node_id == groupId);
        if (groupNode) {
          groupNode.title = groupInfo.title;
          groupNode.tag = groupInfo.tag;

          props.onSenseTreeUpdated(newTree);
        }
      }

    }

    if (headerProps) {
      let elemProps = {
        ...headerProps,
        onDragAndDrop: onGroupHeaderDrangAndDrop,
        onGroupHeaderUpdate: onGroupHeaderUpdate,
        onDeleteGroup: deleteGroup,
        deleteState: deleteState,
      }
      return (<SenseTreeGroupHeader {...elemProps}
      />)
    } else {
      return (<div></div>)
    }

  };

  const onGroupClicked = (group: IGroup) => {
    if (senseTree) {
      const node = findNode(senseTree?.root, (x) => x.node_id == group.key);
      node && props.onSelectedNodeUpdated(node);
    }
  }

  const addGroup = () => {
    if (senseTree) {
      let newTree = Object.assign({}, senseTree);
      newTree.root = addGroupNode(newTree.root);
      newTree.timestamp = Date.now();
      props.onSenseTreeUpdated(newTree);
    }
  }

  const deleteGroup = (group: IGroup) => {
    if (senseTree) {
      const node = findNode(senseTree.root, (x) => x.node_id == group.key);
      if (node) {
        let newTree = Object.assign({}, senseTree);
        let newRoot = removeGroupNode(newTree.root, node);
        newTree.timestamp = Date.now();
        props.onSenseTreeUpdated(newTree);
      }
    }
  }

  const toggleDeleteState = () => {
    setDeleteState(!deleteState);
  }

  if (senseTree) {
    return (
      <div className="p-3"
        style={{ boxShadow: Depths.depth8 }}>
        {/* treeData={props.senseTree.children as TreeItem[]} */}
        <Stack>
          <Stack.Item>
            <Stack horizontal horizontalAlign="end">
              <IconButton
                iconProps={{ iconName: "Add" }}
                onClick={addGroup}
                title="New Group" />
              <IconButton title="Delete"
                iconProps={{ iconName: "Delete" }}
                onClick={toggleDeleteState}
                checked={deleteState}
              />
            </Stack>
          </Stack.Item>
          <Stack.Item>
            <div style={{ height: 468, overflowY: "auto" }}>
              <GroupedList
                items={[]}
                groups={[senseTree.root].map(toGroup)}
                onRenderCell={onRenderCell}
                selectionMode={SelectionMode.none}
                groupProps={
                  {
                    onRenderHeader: onRenderHeader,
                    showEmptyGroups: true,
                    headerProps: {
                      onGroupHeaderClick: onGroupClicked
                    }
                  }
                }
                compact={true}
              />
            </div>
          </Stack.Item>
        </Stack>
      </div>
    )
  } else {
    return null;
  }
}