import { Annotations, Assignments } from "../../datatypes";

export interface ITaskItem {
  name: string;
  checked: boolean;
  nTotal: number;
  nDone: number; nAmbig: number
}


export function compute_progress(items: ITaskItem[]) {
  let { nDone, nTotal } = items.reduce((obj, x) => {
    obj.nDone += x.nDone;
    obj.nTotal += x.nTotal;
    return obj;
  }, { nDone: 0, nTotal: 0 });
  return nDone / nTotal;
}

function summarise_progress(
  annotations: Annotations, assign_id: string) {

  return Object.values(annotations[assign_id].results)
    .reduce((obj, x) => {
      if (x.probs.length > 0) {
        obj.nDone += x.probs[0] >= 0.8 ? 1 : 0;
        obj.nAmbig += x.probs[0] < 0.8 ? 1 : 0;
      }
      return obj;
    }, { nDone: 0, nAmbig: 0 })
}

export function populate_task_items(
  currentAssignId: string,
  assignments: Assignments,
  annotations: Annotations
) {

  return assignments.map((x) => {
    let assign_id = x.assignId;    
    let nTotal = x.items.length;
    let nDone = 0;
    let nAmbig = 0;
    if (assign_id in annotations) {
      ({ nDone, nAmbig } = summarise_progress(
        annotations, assign_id))
    }

    return {
      name: x.assignId,
      nTotal, nDone, nAmbig,
      checked: x.assignId === currentAssignId
    } as ITaskItem;
  });
}


