import React from "react";
import { Stack } from "@fluentui/react";
import { SenseAnnotSession } from "../datatypes";

export interface ISessionInfoProps {
  session: SenseAnnotSession | null,
}

export function SessionInfo(props: ISessionInfoProps) {
  if (props.session !== null) {
    let sess = props.session;
    let last_time = new Date(sess.lastActivity).toLocaleString("en-GB")
    return (
      <Stack id="session-wrapper">
        <div style={{fontSize: "small"}}>Last activity:</div>
        <div style={{fontSize: "small"}}>{last_time}</div>        
      </Stack>
    )
  } else {
    return <Stack id="session-wrapper"></Stack>;
  }

}