import React, { useState, useEffect } from 'react';
import { CwnSense, SenseTree, Annotations, SenseRevision, Trial, SenseTreeNode } from '../datatypes';
import { updateSenses, initSenseTree } from './sorter_updater';
import { SenseList } from './SenseList';
import { SenseTreeView } from './SenseTreeView';
import { RevisionView } from './RevisionView';
import { getGroupedSenses, GroupedSenses, checkSenseTree } from './tree_utils';
import { makeNewSenseRevision, getSenseRevision, cloneSenseRevision } from './sorter_utils';
import { uploadSenseTree } from './sorter_uploader';
import { SessionDispatch } from '../SenseAnnot/sessionUtils/sessionReducer';


export interface ISenseSorterData {
  trial: Trial;
  annotations: Annotations;
  sessionDispatch: SessionDispatch;
  onSenseRevisionUpdated: (trial: Trial, senseRevision: SenseRevision) => void;
}



function SenseSorter(props: ISenseSorterData) {
  // let senseData = props.senseData;  
  const __senseRevision = getSenseRevision(props.trial, props.annotations);  

  const [senses, setSenses] = useState([] as CwnSense[]);
  const [senseTree, setSenseTree] = useState<SenseTree | null>(null);
  const [selectedNode, setSelectedNode] = useState<SenseTreeNode | null>(null);
  const [selectedGroups, setSelectedGroups] = useState<GroupedSenses>({} as GroupedSenses);
  const [senseRevision, setSenseRevision] = useState(__senseRevision);    

  useEffect(() => {    
    const sensePromise = updateSenses(props.trial, setSenses);
    const senseTreePromise = sensePromise
      .then((senses: CwnSense[]) => {
        return initSenseTree(props.trial)
      });
    Promise.all([sensePromise, senseTreePromise])
      .then(([senses, senseTree]) => {
        onSenseTreeUpdated(senseTree, senses);
      }).catch((err) => {
        console.error(err)
      });
  }, [props.trial]);

  // sync groupedSenses with `selectedNode` and `senseTree`
  useEffect(() => {
    let groupedSenses = null;
    if (selectedNode) {
      groupedSenses = getGroupedSenses(selectedNode, senses);
    } else {
      if (senseTree) {
        groupedSenses = getGroupedSenses(senseTree.root, senses);
      } else {
        groupedSenses = { senses: [], groups: [] } as GroupedSenses;
      }
    }    
    setSelectedGroups(groupedSenses);
  }, [selectedNode, senseTree, senseRevision]);

  useEffect(()=>{    
    const __senseRevision = getSenseRevision(props.trial, props.annotations);
    setSenseRevision(__senseRevision);    
  }, [props.trial, props.annotations])

  function onSenseTreeUpdated(newTree: SenseTree | null, senses: CwnSense[]) {
    if (newTree) {
      newTree = checkSenseTree(newTree, senses);
      uploadSenseTree(props.trial, newTree);
    }
    setSenseTree(newTree);
  }

  const onSelectedGroupUpdated = (node: SenseTreeNode | null) => {
    setSelectedNode(node);
  }

  const onSenseRevisionUpdated = (newRevision: SenseRevision) => {
    props.onSenseRevisionUpdated(props.trial, newRevision);    
  }

  let borderStyle = { border: "2px solid black" };  

  return (
    <>
      <div className="row">
        <RevisionView
          key={"rev-" + senseRevision.itemId}
          revision={senseRevision}
          senses={senses}
          senseTree={senseTree}
          sessionDispatch={props.sessionDispatch}
          onSenseRevisionUpdated={onSenseRevisionUpdated} />
      </div>
      <div className="row">
        <div className="col-6">
          <SenseTreeView
            senseTree={senseTree}
            onSelectedNodeUpdated={onSelectedGroupUpdated}
            onSenseTreeUpdated={(senseTree) => onSenseTreeUpdated(senseTree, senses)}
          />
        </div>
        <div className="col-6">
          <SenseList            
            senseTree={senseTree}
            senseRevision={senseRevision}
            groupedSenses={selectedGroups}
            onSenseTreeUpdated={(senseTree) => onSenseTreeUpdated(senseTree, senses)}
            onSenseRevisionUpdated={onSenseRevisionUpdated}
            />
        </div>
      </div>
    </>
  )

}
// key={`sense-list-${senseRevision.timestamp}`}
export default SenseSorter;
