import React, { useRef } from 'react';
import { TooltipHost, TooltipDelay, DirectionalHint, Persona, PersonaInitialsColor, PersonaSize } from '@fluentui/react';
import { CwnSense } from '../datatypes';
import { useDrag, useDrop } from 'react-dnd';

export interface ISenseItemData {
  sense: CwnSense;
  index: number;  
  onDragAndDrop: (srcSenseId: string, tgtSenseId: string)=>void;
  onClick: ()=>void;
}

export interface SenseDragItem {
  type: "leaf-node",
  senseId: string;
}

function examplesTooltips(examples: string[]) {
  return {
    onRenderContent: () => {
      return (<ul>
        {examples.map((x) => (
          <li key={x.substr(0, 5)+x.length.toString()}>{x}</li>))}
      </ul>)
    }
  }
}

export function SenseItem(props: ISenseItemData) {
  const { sense, index } = props;
  const ref = useRef<HTMLDivElement>(null);

  const [{}, drag] = useDrag({
    item: { type: "leaf-node", senseId: sense.cwn_id },
    collect: (monitor) => ({
      isDragging: !!monitor.isDragging(),
    }),
  });

  const [{}, drop] = useDrop({
    accept: "leaf-node",
    drop: (item: SenseDragItem, monitor) => {
      // console.log(item, monitor);
      props.onDragAndDrop(item.senseId, sense.cwn_id);
    },

    canDrop: (item, monitor) => {
      return true;
    },
  });

  drag(drop(ref));
  if (sense && index !== undefined) {
    return (
      <div ref={ref}>
        <TooltipHost
          tooltipProps={examplesTooltips(sense.examples)}
          id={"tp" + sense.cwn_id}
          delay={TooltipDelay.long}
          directionalHint={DirectionalHint.rightCenter}
        >
          <Persona
            key={`sense-item-${sense.cwn_id}`}
            imageInitials={sense.pos}
            initialsColor={(index % 6) as PersonaInitialsColor}
            text={sense.definition}
            secondaryText={sense.examples[0]}
            size={PersonaSize.size32}            
            showSecondaryText={true}
            onClick={props.onClick}            
            styles={{
              root: { marginTop: "7px", userSelect: "none" },
              primaryText: { fontSize: "11pt", userSelect: "none" },
              secondaryText: { fontSize: "8pt", color: "gray", userSelect: "none" }
            }}
          />
        </TooltipHost>
      </div>
    )
  } else {
    return (<div></div>)
  }

}
export default SenseItem;